import React from 'react'
import Waypoint from 'react-waypoint'
import scrollToComponent from 'react-scroll-to-component';
import './product-range-section.scss'
import image from './PNG/product-range.png'

class DeliverySection extends React.Component{
    constructor(props){
        super(props);
        this.state = {

            sectionOneEnter: false,
            sectionTwoEnter: false,
            sectionThreeEnter: false,
        }
        this._handleWaypointEnter = this._handleWaypointEnter.bind(this);
        this._handleWaypointLeave = this._handleWaypointLeave.bind(this);
    }
    _handleWaypointEnter(){
        console.log(this.state.sectionOneEnter);
        console.log("yes here");
        scrollToComponent(this.view, { offset: 0, align: 'top', duration: 1000});
        this.setState({
            sectionOneEnter: true
        })
    }
    _handleWaypointLeave(){
        console.log('viewed..!');
        this.setState({
            sectionOneEnter: false
        })
    }
    render(){
        return(
        <Waypoint onEnter={this._handleWaypointEnter} onLeave={this._handleWaypointLeave}>
            <div ref={(section) => { this.view = section; }} className={"section product-range-section" + (this.state.sectionOneEnter ? " fade-in" : " fade-out")}>
                <div className="container">
                    <h1 className="section-title">REVTECH achieved significant <span>efficiency in procurement</span> by sourcing all essential supplies from one partner</h1>
                    <div className="row">
                        <div className="col-5">
                            <p className="section-lead">Revtech eliminated inconsistency in their supplies by procuring all office needs, from basic stationery to hig-end electronics, pantry eatables to cleaning supplies htorugh us with flexible payment options</p>
                            <p>(read credit options)</p>
                        </div>
                        <div className="col-10">
                            <img src={image} className={this.state.sectionOneEnter ? "section-thumb active" : "section-thumb"}/>
                        </div>
                    </div>
                </div>
            </div>
        </Waypoint>
        )
    }
}

export default DeliverySection;


